// *********************************************************************************** //
// * Set initial map view                                                            * //
// *********************************************************************************** //
const newYorkOn1366x768Monitor = [43.16512263158296, -75.8111572265625];
const newYorkOnLargeMonitor = [42.767178634023345, -76.04736328125];
const map = new L.Map('map', {
  center: newYorkOnLargeMonitor,
  zoom: 8,
  cursor: true,
  attributionControl: false,
});

// Add the first layer to the map on page load
map.addLayer(MtbMap);

// Add and remove the street data on zoom level
map.addEventListener('zoomend', (event) => {
  if (map.getZoom() >= 10) {
    map.addLayer(Stamen_TonerHybrid);
  }
  if (map.getZoom() <= 8) {
    map.removeLayer(Stamen_TonerHybrid);
  }
});

// map.addLayer(Stamen_TonerLabels);
window.map = map;


// *********************************************************************************** //
// * Google Sheets API                                                               * //
// *********************************************************************************** //
const gSheet = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vTlYv8lV3OwXacqtktj-eHE6aka6Ug91uLSrFKQklm3brYPG6v319tiC7gR91fEg8cdWggkl2C5-Mjy/pub?gid=0&single=true&output=csv';

async function asyncData() {
  const response = await fetch(gSheet);
  const data = await response.text();
  return data;
}

const returnedData = asyncData();

returnedData.then((dataResponse) => {
  const allTextLines = dataResponse.split(/\r\n|\n/);
  // Split per line on tabs and commas
  const headers = allTextLines[0].split(/\t|,/);

  // Total JOA cases
  const returnJOACases = (joa, casesOrDeaths, calculateStateTotals = false) => {
    const cases = [];
    const deaths = [];
    countyData.features.map((entries) => {
      // if the JOA is not declared, then calculate totals for all of NYS
      if (entries.properties.JOA === joa ? joa : calculateStateTotals) {
        cases.push(entries.properties.cases);
        deaths.push(entries.properties.deaths);
      }
    });
    const totalCases = cases.reduce((total, amount) => parseInt(total, 10) + parseInt(amount, 10));
    const totalDeaths = deaths.reduce((total, amount) => parseInt(total, 10) + parseInt(amount, 10));
    if (casesOrDeaths === 'cases') {
      return totalCases.toLocaleString();
    }
    if (casesOrDeaths === 'deaths') {
      return totalDeaths.toLocaleString();
    }
  };

  // ? County Labels
  function countyLabel(lat, lng, county) {
    const cLabel = new L.Marker([lat, lng], {
      interactive: false,
      icon: new L.DivIcon({
        className: 'my-div-icon',
        html: `<span class="my-div-span">${county}</span>`,
        // Anchor the county name label by its length
        iconAnchor: [county.length * 3, county.length],
      }),
    }).addTo(map);
    return cLabel;
  }

  // ? JOA Labels
  const JOAMapLabels = {
    JOA1: {
      label: 'JOA-1',
      color: 'white',
      latlng: [40.450127, -73.40515],
      // bounds: [[39.590873865955906, -75.03387451171876], [42.42142901536395, -71.15570068359376]],
      bounds: [[39.9347154734727, -74.58261108398439], [41.87879218716907, -71.86788940429689]],
    },
    JOA2: {
      label: 'JOA-2',
      color: 'green',
      latlng: [41.57025, -73.27332],
      // bounds: [[40.48664852056083, -76.13250732421875], [43.279204926082784, -72.25433349609376]],
      bounds: [[40.47895111753169, -75.90838623046876], [42.94774245766811, -72.41802978515626]],
    },
    JOA3: {
      label: 'JOA-3',
      color: 'blue',
      latlng: [43.89789, -73.10873],
      // bounds: [[45.03471, -73.3667], [42.31591, -74.97994]],
      bounds: [[42.14788004324458, -76.64392089843751], [45.33313971737933, -71.99011230468751]],
    },
    JOA4: {
      label: 'JOA-4',
      color: 'black',
      latlng: [43.654729, -76.5433850],
      // bounds: [[41.84501267270692, -77.65411376953126], [44.57873024377564, -73.77593994140626]],
      bounds: [[41.90799426822956, -76.55239105224611], [44.427742640041835, -75.1274085044861]],
    },
    JOA5: {
      label: 'JOA-5',
      color: 'red',
      latlng: [41.87228, -77.59980],
      // bounds: [[41.43860847395721, -79.92828369140625], [44.190082025040525, -76.05010986328126]],
      bounds: [[41.72606091413639, -79.88063964843751], [43.4606091413639, -76.38063964843751]],
    },
  };


  Object.entries(JOAMapLabels).forEach((j) => {
    const joa = j[1];
    const joaIdSVG = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
    joaIdSVG.setAttribute('xmlns', 'http://www.w3.org/2000/svg');
    joaIdSVG.setAttribute('viewBox', '0 0 100 50');
    joaIdSVG.innerHTML = `
        <rect width="100" height="50" fill="salmon" stroke="${joa.color}" stroke-width="3" fill-opacity="0.5"/>
        <text x="50" y="25" fill="black" font-weight="bold" font-family="Arial" font-size="24" text-anchor="middle" dominant-baseline="central">${joa.label}</text>`;
    joaIdSVG.setAttributeNS(null, 'class', 'leaflet-grid-label');
    const joaLL = new L.LatLng(joa.latlng[0], joa.latlng[1]);
    // Add or subtract a small number on the center latitudes/longitudes, this will give us a legitimate new LatLngBounds
    // Add the pad() method at the end to add padding on all sides of the new boundaries so the JOA label can fit
    const centerBounds = new L.LatLngBounds([joaLL.lat + 0.01, joaLL.lng - 0.01], [joaLL.lat - 0.01, joaLL.lng + 0.01]).pad(10.5);
    const joaLabel = new L.svgOverlay(joaIdSVG, centerBounds, {
      interactive: true,
    });
    // Clicking on the JOA label will fit the map to its bounds!
    joaLabel.on('click', () => {
      map.flyToBounds(joa.bounds, {
        maxZoom: 12,
      });
    });
    joaLabel.addTo(map);
  });

  // Format the CSV data and add to the map
  for (let i = 0; i < allTextLines.length; i += 1) {
    const data = allTextLines[i].split(/\t|,/);

    if (data.length === headers.length) {
      countyData.features.forEach((k) => {
        const countyName = data[3];
        const cases = data[4];
        const deaths = data[5];
        if (k.properties.name === countyName) {
          // Set the cases and death properties one time
          // This will not be needed once the interval runs again
          k.properties.cases = cases;
          k.properties.deaths = deaths;
          if (countyName === 'Cayuga') {
            countyLabel(42.7626, -76.5534, countyName);
          } else if (countyName === 'Chemung') {
            countyLabel(data[0], [data[1] - 0.04], countyName);
          } else if (countyName === 'Chautauqua') {
            countyLabel(data[0] - 0.1, data[1], countyName);
          } else if (countyName === 'Herkimer') {
            countyLabel(data[0] - 0.4, data[1] - 0.1, countyName);
          } else if (countyName === 'New York City') {
            countyLabel(40.750, -73.8, countyName);
          } else {
            countyLabel(data[0], data[1], countyName);
          }
        }
      });
    }
  }

  // ? Leaflet Plugin - Bottom Left COVID-19 Numbers for all JOAs
  // Bottom right Leaflet Control menu
  const JOAInfo = L.control({ position: 'bottomleft' });

  JOAInfo.onAdd = function () {
    this._div = L.DomUtil.create('div', 'info');
    this.update();
    return this._div;
  };

  JOAInfo.update = function () {
    this._div.innerHTML = `
    <div class="mdc-data-table JOADataTable mdc-elevation--z12">
      <table class="mdc-data-table__table" aria-label="Dessert calories">
        <thead>
          <tr class="mdc-data-table__header-row">
            <th class="mdc-data-table__header-cell" role="columnheader" scope="col"></th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">JOA-1</th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">JOA-2</th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">JOA-3</th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">JOA-4</th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">JOA-5</th>
            <th class="mdc-data-table__header-cell mdc-data-table__header-cell--numeric" role="columnheader" scope="col">Total NYS</th>
          </tr>
        </thead>
        <tbody class="mdc-data-table__content">
          <tr class="mdc-data-table__row totalJOACases">
            <td class="mdc-data-table__cell"><b>Total Positives</b></td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(1, 'cases')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(2, 'cases')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(3, 'cases')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(4, 'cases')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(5, 'cases')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(null, 'cases', true)}</td>
          </tr>
          <tr class="mdc-data-table__row totalJOADeaths">
            <td class="mdc-data-table__cell"><b>Total Deaths</b></td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(1, 'deaths')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(2, 'deaths')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(3, 'deaths')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(4, 'deaths')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(5, 'deaths')}</td>
            <td class="mdc-data-table__cell mdc-data-table__cell--numeric">${returnJOACases(null, 'deaths', true)}</td>
          </tr>
        </tbody>
      </table>
    </div>`;
  };

  JOAInfo.addTo(map);

  // Add cornflower blue styling to the Total NYS row in the bottom left data table
  const totalNYS = {
    get totalNYSLabel() {
      return document.querySelector('.JOADataTable > table > thead > tr').lastElementChild;
    },
    get totalNYSCases() {
      return document.querySelector('.totalJOACases').lastElementChild;
    },
    get totalNYSDeaths() {
      return document.querySelector('.totalJOADeaths').lastElementChild;
    },
  };

  const totalNYSStyles = 'background-color: cornflowerblue; color: white; font-weight: bold';

  Object.values(totalNYS).forEach((element) => {
    element.setAttributeNS(null, 'style', totalNYSStyles);
  });

  // This is broken
  // setInterval(() => {
  //   console.log(`Updating JOA Chart at ${Date().toLocaleString()}`);
  //   JOAInfo.update();
  // }, 300000);
});


// *********************************************************************************** //
// * Material Design Components                                                      * //
// *********************************************************************************** //
const topAppBar = new MDCTopAppBar(document.querySelector('.mdc-top-app-bar'));
const iconButtonRipple = new MDCRipple(document.querySelector('.mdc-icon-button'));
const menuSurface = new MDCMenuSurface(document.querySelector('.mdc-menu-surface'));
const dataTable = new MDCDataTable(document.querySelector('.mdc-data-table.dtSurfaceMenu'));

iconButtonRipple.unbounded = true;

// The surface menu button in the top app bar
iconButtonRipple.listen('click', () => {
  if (menuSurface.isOpen()) {
    menuSurface.close();
  } else {
    menuSurface.open();
  }
  menuSurface.setAbsolutePosition(20, 60);
});


// Dynamically add Strike Team data to the Top App Bar Surface Menu
strikeTeams.forEach((team, i) => {
  const table = document.querySelector('#strikeTeamDataTable');
  const { missionNumber } = team;
  const { DLAN } = team;
  const { unit } = team;
  const { strikeTeam } = team;
  const { location } = team;
  const { pax } = team;
  const { vix } = team;
  const { mission } = team;
  const { s4Lodging } = team;
  const { s4Vic } = team;
  const { s4Chow } = team;
  const { lat } = team;
  const { lng } = team;
  const { active } = team;
  const dataBody = document.querySelector('.dtSurfaceMenuBody');
  const dataRow = table.insertRow(0);
  dataRow.classList.add('mdc-data-table__row', `mission_${i}`);
  dataBody.appendChild(dataRow);

  const missionNumberRow = dataRow.insertCell(0);
  missionNumberRow.classList.add('mdc-data-table__cell', 'mdc-data-table__cell--numeric');
  missionNumberRow.textContent = missionNumber;

  const DLANRow = dataRow.insertCell(1);
  DLANRow.classList.add('mdc-data-table__cell', 'mdc-data-table__cell--numeric');
  DLANRow.textContent = DLAN;

  const unitRow = dataRow.insertCell(2);
  unitRow.classList.add('mdc-data-table__cell');
  unitRow.textContent = unit;

  const strikeTeamRow = dataRow.insertCell(3);
  strikeTeamRow.classList.add('mdc-data-table__cell');
  strikeTeamRow.setAttributeNS(null, 'style', 'text-align: center;');
  strikeTeamRow.textContent = strikeTeam;

  const locationRow = dataRow.insertCell(4);
  locationRow.classList.add('mdc-data-table__cell');
  locationRow.textContent = location;

  const paxRow = dataRow.insertCell(5);
  paxRow.classList.add('mdc-data-table__cell', 'mdc-data-table__cell--numeric');
  paxRow.textContent = pax;

  const vixRow = dataRow.insertCell(6);
  vixRow.classList.add('mdc-data-table__cell', 'mdc-data-table__cell--numeric');
  vixRow.textContent = vix;

  const missionRow = dataRow.insertCell(7);
  missionRow.classList.add('mdc-data-table__cell');
  missionRow.textContent = mission;

  const S4LodgingRow = dataRow.insertCell(8);
  S4LodgingRow.classList.add('mdc-data-table__cell');
  if (s4Lodging.R && s4Lodging.A && s4Lodging.G) {
    S4LodgingRow.setAttributeNS(null, 'style', 'background-color: green; border: 1px rgba(0,0,0,0.12) solid;');
  } else if (s4Lodging.R === false && s4Lodging.A === false && s4Lodging.G === false) {
    S4LodgingRow.setAttributeNS(null, 'style', 'background-color: red; border: 1px rgba(0,0,0,0.12) solid;');
  } else {
    S4LodgingRow.setAttributeNS(null, 'style', 'background-color: orange; border: 1px rgba(0,0,0,0.12) solid;');
  }


  const S4VicRow = dataRow.insertCell(9);
  S4VicRow.classList.add('mdc-data-table__cell');
  if (s4Vic.R && s4Vic.A && s4Vic.G) {
    S4VicRow.setAttributeNS(null, 'style', 'background-color: green; border: 1px rgba(0,0,0,0.12) solid;');
  } else if (s4Vic.R === false && s4Vic.A === false && s4Vic.G === false) {
    S4VicRow.setAttributeNS(null, 'style', 'background-color: red; border: 1px rgba(0,0,0,0.12) solid;');
  } else {
    S4VicRow.setAttributeNS(null, 'style', 'background-color: orange; border: 1px rgba(0,0,0,0.12) solid;');
  }

  const S4ChowRow = dataRow.insertCell(10);
  S4ChowRow.classList.add('mdc-data-table__cell');
  if (s4Chow.R && s4Chow.A && s4Chow.G) {
    S4ChowRow.setAttributeNS(null, 'style', 'background-color: green; border: 1px rgba(0,0,0,0.12) solid;');
  } else if (s4Chow.R === false && s4Chow.A === false && s4Chow.G === false) {
    S4ChowRow.setAttributeNS(null, 'style', 'background-color: red; border: 1px rgba(0,0,0,0.12) solid;');
  } else {
    S4ChowRow.setAttributeNS(null, 'style', 'background-color: orange; border: 1px rgba(0,0,0,0.12) solid;');
  }

  // enable click on data row and fly to unit
  document.querySelector(`.mdc-data-table__row.mission_${i}`).addEventListener('click', () => {
    map.flyTo([lat, lng], 15);
  });
});

// *********************************************************************************** //
// * Leaflet Plugin - Marker Cluster                                                 * //
// *********************************************************************************** //
// Unit locations represented as markers
const markerClusters = L.markerClusterGroup();

for (let i = 0; i < strikeTeams.length; i += 1) {
  const popup = `
    <b class="mdc-typography--headline5"><u>Unit:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].unit}</span><br/>
    <b class="mdc-typography--headline5"><u>Strike Team:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].strikeTeam}</span><br/>
    <b class="mdc-typography--headline5"><u>Location:</u> </b>
    <span class="mdc-typography--headline5">${strikeTeams[i].location}</span> <br/>
    <b class="mdc-typography--headline5"><u>DLAN:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].DLAN}</span> <br/>
    <b class="mdc-typography--headline5"><u>PAX:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].pax}</span><br/>
    <b class="mdc-typography--headline5"><u>VIX:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].vix}</span><br/>
    <b class="mdc-typography--headline5"><u>Mission:</u> </b>
      <span class="mdc-typography--headline5">${strikeTeams[i].Mission}</span><br/>`;

  if (strikeTeams[i].lat) {
    const m = L.marker([strikeTeams[i].lat, strikeTeams[i].lng], {
      icon: L.divIcon({
        className: `missionIcon  ${strikeTeams[i].active ? 'missionIcon--active' : 'missionIcon--rest'}`,
        html: `<span>${strikeTeams[i].strikeTeam}</span>`,
      }),
    }).bindPopup(popup);
    markerClusters.addLayer(m);
  }
}

