// control that shows county info on hover
const info = L.control({ position: 'topright' });
window.info = info;
info.onAdd = function (map) {
  this._div = L.DomUtil.create('div', 'info');
  this.update();
  return this._div;
};

info.update = function (props) {
  this._div.innerHTML = `<h4>NY COVID-19 Cases</h4>${props ? `<b>${props.name}</b><br />${props.cases} positives <br /> ${props.deaths} deaths` : 'Hover over a county'}`;
};

info.addTo(map);


// *********************************************************************************** //
// * NYS County Boundaries                                                           * //
// *********************************************************************************** //
// get color depending on COVID-19 cases value
function getColor(d) {
  if (d > 1000) {
    return '#800026';
  }
  if (d > 500) {
    return '#BD0026';
  }
  if (d > 200) {
    return '#E31A1C';
  }
  if (d > 100) {
    return '#FC4E2A';
  }
  if (d > 50) {
    return '#FD8D3C';
  }
  if (d > 20) {
    return '#FEB24C';
  }
  if (d >= 10) {
    return '#FED976';
  }
  return '#FFEDA0';
}

function countyOutlineStyle(feature) {
  return {
    weight: 2,
    opacity: 1,
    color: 'white',
    dashArray: '3',
    fillOpacity: 0.7,
    fillColor: getColor(feature.properties.cases),
  };
}

function highlightFeature(e) {
  const layer = e.target;
  layer.setStyle({
    weight: 5,
    color: '#666',
    dashArray: '',
    fillOpacity: 0.7,
  });

  if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
    layer.bringToFront();
  }
  info.update(layer.feature.properties);
}

let geojson;

function resetHighlight(e) {
  // This will ensure the JOA boundaries are above the dash array on selected counties
  e.target.bringToBack();
  geojson.resetStyle(e.target);
  info.update();
}

function zoomToFeature(e) {
  // Prevent the map from zooming out at ZL 15. This is helpful when dealing with marker clusters
  if (map.getZoom() >= 15) {
    return false;
  }
  map.flyToBounds(e.target.getBounds(), { duration: 0.25 });
}

function onEachFeature(feature, layer) {
  layer.on({
    mouseover: highlightFeature,
    mouseout: resetHighlight,
    click: zoomToFeature,
  });
}

geojson = L.geoJson(countyData, {
  style: countyOutlineStyle,
  onEachFeature,
}).addTo(map);


// *********************************************************************************** //
// * Leaflet Plugin - COVID-19 Number of Cases Legend                                * //
// *********************************************************************************** //
const legend = L.control({ position: 'bottomright' });

legend.onAdd = function (map) {
  const div = L.DomUtil.create('div', 'info legend');
  const grades = [0, 10, 20, 50, 100, 200, 500, 1000];
  const labels = [];
  let from;
  let to;

  for (let i = 0; i < grades.length; i += 1) {
    from = grades[i];
    to = grades[i + 1];

    labels.push(`<i style="background:${getColor(from + 1)}"></i> ${from}${to ? `&ndash;${to}` : '+'}`);
  }

  div.innerHTML = labels.join('<br>');
  return div;
};

legend.addTo(map);


// *********************************************************************************** //
// * Adjust Leaflet Controls for Top App Bar                                         * //
// *********************************************************************************** //
// Move the zoom control and cases/deaths container down so the top app bar is not covering it
const mapHalfHeight = map.getSize().y / 2;
const zoomContainer = map.zoomControl.getContainer();
const casesAndDeathsContainer = map._controlContainer.querySelector('.leaflet-top.leaflet-right');
const zoomContainerHalfHeight = parseInt(zoomContainer.offsetHeight + 525, 10);
const containerTop = `${mapHalfHeight - zoomContainerHalfHeight}px`;
zoomContainer.style.position = 'absolute';
zoomContainer.style.top = containerTop;
casesAndDeathsContainer.style.position = 'absolute';
casesAndDeathsContainer.style.top = containerTop;


// *********************************************************************************** //
// * JOA Boundaries Outline                                                          * //
// *********************************************************************************** //
[JOA1, JOA2, JOA3, JOA4, JOA5].forEach((joa) => {
  L.geoJSON(joa, {
    style() {
      if (joa === JOA1) {
        return {
          color: 'white',
          weight: 5,
        };
      }
      if (joa === JOA2) {
        return {
          color: 'green',
          weight: 5,
        };
      }
      if (joa === JOA3) {
        return {
          color: 'blue',
          weight: 5,
        };
      }
      if (joa === JOA4) {
        return {
          color: 'black',
          weight: 5,
        };
      }
      if (joa === JOA5) {
        return {
          color: 'red',
          weight: 5,
        };
      }
      return true;
    },
  }).addTo(map);
});

